import { createRouter, createWebHashHistory } from 'vue-router'
import store from './store.js'
import NotFound from '@/views/404.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/Home.vue'),
  }, 

  {
    path: '/:type(mall|jdmall|wymall)/category',
    name: 'Category',
    component: () => import('@/views/Mall/Category.vue'),
    props: route => ({ type: route.params.type, id: route.query.id })
  },
  {
    path: '/:type(mall|jdmall|wymall)/list',
    name: 'List',
    component: () => import('@/views/Mall/List.vue'),
    props: route => ({ type: route.params.type, cid: route.query.cid })
  },
  {
    path: '/:type(mall|jdmall|wymall)/item',
    name: 'Item',
    component: () => import('@/views/Mall/Item.vue'),
    props: route => ({ type: route.params.type, id: route.query.id })
  },
  {
    path: '/:type(mall|jdmall|wymall)/cart',
    name: 'Cart',
    component: () => import('@/views/Mall/Cart.vue'),
    props: route => ({ type: route.params.type })
  },
  {
    path: '/:type(mall|jdmall|wymall)/bill',
    name: 'Bill',
    component: () => import('@/views/Mall/Bill.vue'),
    props: route => ({ type: route.params.type, direct: route.query.direct, aid: route.query.aid }),
    meta: {requiresAuth: true},
  },


  {
    path: '/invite',
    name: 'Invite',
    component: () => import('@/views/Invite.vue'),
  },

  {
    path: '/my',
    name: 'My',
    component: () => import('@/views/My/Index.vue'),
  },
  {
    path: '/my/profile',
    name: 'MyProfile',
    component: () => import('@/views/My/Profile.vue'),
    meta: {requiresAuth: true}
  },
  {
    path: '/my/invite',
    name: 'MyInvite',
    component: () => import('@/views/My/Invite.vue'),
    meta: {requiresAuth: true}
  },
  {
    path: '/my/wallet',
    name: 'MyWallet',
    component: () => import('@/views/My/Wallet.vue'),
    meta: {requiresAuth: true}
  },
  {
    path: '/my/:action(deposit|withdrawal)+',
    name: 'MyWalletCash',
    component: () => import('@/views/My/WalletCash.vue'),
    props: route => ({ action: route.params.action }),
    meta: {requiresAuth: true}
  },
  {
    path: '/my/point',
    name: 'MyPoint',
    component: () => import('@/views/My/Point.vue'),
    meta: {requiresAuth: true}
  },

  {
    path: '/my/order',
    name: 'Order',
    component: () => import('@/views/Mall/Order.vue'),
    meta: {requiresAuth: true}
  },
  {
    path: '/my/interest',
    name: 'MyInterest',
    component: () => import('@/views/Mall/Interest.vue'),
    meta: {requiresAuth: true}
  },
  {
    path: '/my/address/:action(choose|add|edit)?',
    name: 'MyAddress',
    component: () => import('@/views/Mall/Address.vue'),
    props: route => ({ action: route.params.action, id: route.query.id, target: route.query.target }),
    meta: {requiresAuth: true},
  },
  

  {
    path: '/vip',
    name: 'VIP',
    component: () => import('@/views/VIP.vue'),
  },
  {
    path: '/:action(login|logout|register|changepassword|bind|invite)+/:ucode?',
    name: 'Passport',
    component: () => import('@/views/Passport.vue'),
    props: route => ({ action: route.params.action, ucode: route.params.ucode, code: route.query.code, subtitle: route.query.subtitle, redirect: route.query.redirect }),
  },

  {
    path: '/:page(feature|intro|guide|help|about|contact|terms)+',
    name: 'Page',
    component: () => import('@/views/Page.vue'),
    props: true,
  },
  {
    path: '/:path*',
    component: NotFound
  }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from) => {
  if (to.meta.requiresAuth && !store.getters.authorized) {
    console.warn("需要登录授权");
    return {
      path: '/login',
      query: {redirect: to.fullPath},
    }
  }
  store.commit("toggleTabbar", "Passport|Strategy".indexOf(to.name) == -1);
  return true;
});

router.afterEach((to, from) => {
  const toDepth = to.path.split('/').length
  const fromDepth = from.path.split('/').length
  to.meta.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
});

export default router
